.header-bg-img {
    width: 100%;
    height: auto;
}

:root {
    --profile-height: 360px;
    --profile-offset: calc(var(--profile-height) / -1.5)
}

.HeaderContainer {
    margin-bottom: calc(var(--profile-offset));
}

.profile-container {
    z-index: 2;
    position: relative;
    top: calc(var(--profile-offset));
}

.profile-img {
    display: block;
    margin-left: auto;
    margin-right: auto;

    border-radius: calc(var(--profile-height) / 2) calc(var(--profile-height) / 2) 0 0;
    border: 2px solid white;

    width: auto;
    height: calc(var(--profile-height));
}

@media screen and (max-width: 768px) {
    .header-bg-img {
        display: none;
    }

    .HeaderContainer {
        margin-bottom: 0px;
    }

    .profile-container {
        position: static;        
    }
    
    .profile-img {
        width: 100%;
        height: auto;
        border-radius: 0;
        border-style: none;
    }
}
