.LandingCarousel {
    background-color: #212121;
    color: white;
    min-height: 400px !important;
}

.LandingCarousel > .slick-dots {
    position: absolute;
    bottom: 10px;
    display: block;
    width: 100%;
    padding: 10;
    margin: 10;
    list-style: none;
    text-align: center;
}

.LandingCarousel > .slick-dots li button::before {
    font-size: 16px;
    color: #fff;
    opacity: 0.7;
  }

.LandingCarousel > .slick-dots li.slick-active button::before {
    color: #fff;
    opacity: 1;
  }

@media screen and (max-width: 640px) {
    .LandingCarousel {
        font-size: 0.9rem;
        min-height: 500px !important;
    }

    .StyleSlide > .btn {
        font-size: 0.9rem;
    }

    .StyleSlide > h3 {
        font-size: 1.4rem;
    }

    .StyleSlide > h5 {
        font-size: 1.15rem;
    }
}

ul.star-list {
    list-style: none;
    margin-left: 0;
    padding-left: 0;
}

ul.star-list > li {
    padding-left: 1em;
    text-indent: -1em;
}

ul.star-list > li:before {
    content: "\2605";
    padding-right: 5px;
}
